import login from '@/views/login' //Layout 
import errorPage from '@/views/errorPage' //Layout 
const staticRouter = [{
  "path": "/",
  "component":"login",
  "redirect": "login",
  "children": [{
    "path": "login",
    "component": "login/index",
    "meta": {
      "title": "",
      "icon": "login"
    }
  }],
  "hidden": true
}, {
  "path": "*",
  "component": "errorPage",
  "redirect": "/404",
  "children": [{
    "path": "404",
    "name": "404",
    "component": "errorPage/index",
    "meta": {
      "title": "404",
      "icon": "form"
    }
  }],
  "hidden": true
}]

export default staticRouter