import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
import {LGI,LSI,SGI,SSI} from "../utils/browserStorage"

export default new Vuex.Store({
  state: {
    sidebarIsCollapse:LGI("sidebarIsCollapse")==true?true:false
  },
  mutations: {
    SET_SIDEBAR_IS_COLLAPSE(state){
      state.sidebarIsCollapse = !state.sidebarIsCollapse;
      LSI("sidebarIsCollapse",state.sidebarIsCollapse)
    }
  },
  actions: {
    sidebarIsCollapseAction({commit}){
      commit("SET_SIDEBAR_IS_COLLAPSE")
    }
  },
  modules: {
  }
})
